.t-wrapper{
    padding:  0 3rem 0 3rem;;
    /* height: 100vh; */
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 7rem;
    flex-direction: column;
    gap: 2rem;
    position: relative;
}
.t-heading{
    align-self: start;
}
.t-heading>*{
    font-size: 2rem;
    font-weight: bold;
}
.t-heading span:nth-of-type(2){
    color: var(--orange);
}

.t-blur1{
    left: 38rem;
    top: 16rem;
}
.t-blur2{
    top: 9rem;
    left: 0;
}
.testimonial{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding: 2rem;
    width: 30rem;
    height: -moz-fit-content;
    height: fit-content;
    /* styles same as work.css */
    background: rgba(255, 255, 255, 0.26);
    border: 7px solid var(--blueCard);
    box-shadow: var(--boxShadow);
    border-radius: 20px;
}
.testimonial>img{
    width: 6rem;
    height: 6rem;
    border-radius: 100%;
    margin-bottom: 30px;
}
.testimonial>span{
    color: var(--gray);
}


/* slider */
.t-wrapper .swiper {
    width: 100%;
    height: 70%;
}

.t-wrapper .swiper-slide{
    display: flex;
    align-items: center;
    justify-content: center;
}

.t-wrapper .swiper-pagination-bullet-active{
    background: var(--orange);
}
.swiper-pagination-bullet{
    background-color: transparent;
    opacity: 100%;
    border: solid 2px var(--orange);
    margin:5px;
}

.t-wrapper iframe{
    width: 175px;
    height: 315px;
}

.swiper-slide div{
    display: flex;
    justify-content: center;
}

@media screen and (max-width: 480px){
    .swiper-slide {
        height: 75%;
        
    }
    .t-wrapper{
        padding: 0;
        margin-top: 3rem;
    }
    .t-wrapper .swiper-slide{
        align-items: baseline;
    }
    .testimonial>img{
        width: 70px;
        height: 70px;
    }
    .review{
        font-size: 15px;
        padding: 15px 5px;
    }
    .testimonial{
        width: 15rem;
        padding:1rem;
    }
    .swiper-horizontal>.swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction {
        bottom: 118px;
    }
    .t-wrapper .swiper {
        height: 55%;
    }
  }